/* eslint-disable jsx-a11y/iframe-has-title */
import React from 'react';
import './ContactS.scss'
import RoomIcon from '@material-ui/icons/Room';
import PhoneIphoneIcon from '@material-ui/icons/PhoneIphone';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
const Contact = () => {
    return (
        <div className="contacts_sec" id="contact">
            <div className="this_section">
                <h1>Contact</h1>
            </div>
            <div className="contacts">
                <div className="left_box">
                    <div className="location parent_div">
                        <div className="location_icon icon">
                            <RoomIcon />
                        </div>
                        <div className="location_text">
                            <h1>Location:</h1>
                            <p>Tashkent Uchtepa, 14 2</p>
                        </div>
                    </div>
                    <div className="email parent_div">
                        <div className="mail_icon icon">
                            <MailOutlineIcon />
                        </div>
                        <div className="mail_text">
                            <h1>Email:</h1>
                            <p>developerdiyorbek@gmail.com</p>
                        </div>
                    </div>
                    <div className="call parent_div">
                        <div className="phone_icon icon">
                            <PhoneIphoneIcon />
                        </div>
                        <div className="mail_text">
                            <h1>Call:</h1>
                            <p>+998 33 290 70 80 </p>
                            <p>+998 90 806 20 04 </p>
                        </div>
                    </div>
                    <div className="map">
                    <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2998.457592163538!2d69.20323761496154!3d41.277144510785845!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x38ae8bf881c126a5%3A0x33070ad3072036f5!2s%22SOFT-PLUS%22%20Xususiy%20korxonasi!5e0!3m2!1sru!2s!4v1624293339457!5m2!1sru!2s" width="400" height="300"  loading="lazy" />
                    </div>
                </div>
                <div className="right_box">
                    <form >
                    <div className="first_inp">
                        <div className="name">
                            <p>Your name:</p>
                            <input type="text"  />
                        </div>
                        <div className="second_inp">
                            <div className="name">
                                <p>Your Email:</p>
                                <input type="mail" />
                            </div>
                        </div>
                      </div>
                      <div className="third_inp">
                          <p>Title:</p>
                          <input type="text" />
                      </div>
                      <div className="fourth_inp">
                          <p>Text:</p>
                          <textarea></textarea>
                      </div>
                      <button type="submit">Send message</button>
                    </form>
                </div>
            </div>
        </div>
    );
};


export default Contact;