/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import './FixedNavigation.scss'
import hero2 from '../../assets/hero2.jpg'
import TelegramIcon from '@material-ui/icons/Telegram';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import TwitterIcon from '@material-ui/icons/Twitter';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import HouseIcon from '@material-ui/icons/House';
import PermIdentityIcon from '@material-ui/icons/PermIdentity';
import InsertDriveFileIcon from '@material-ui/icons/InsertDriveFile';
import EmailIcon from '@material-ui/icons/Email';
import CloseIcon from '@material-ui/icons/Close';
import MenuIcon from '@material-ui/icons/Menu';

const Navigation = (props) => {

    const forOpen = () =>{
        const mainnav = document.getElementById('mainnav');
        const forClose = document.getElementById('forClose');
        const forOpenn = document.getElementById('forOpen');
        mainnav.style.left = "0";
        forClose.style.zIndex = "10";
        forOpenn.style.zIndex = "-10"
    }
    const forClose = () =>{
        const mainnav = document.getElementById('mainnav');
        const forClose = document.getElementById('forClose');
        const forOpenn = document.getElementById('forOpen');
        mainnav.style.left = "-100%";
        forClose.style.zIndex = "-10";
        forOpenn.style.zIndex = "10"
    }
    
    const quickLinkHome = () =>{
        const forOpenn = document.getElementById('forOpen');
        const forClose = document.getElementById('forClose');
        const mainnav = document.getElementById('mainnav');
        forOpenn.style.zIndex = "10"
        forClose.style.zIndex = "-10";
        mainnav.style.left = "-100%"
    }
    const quickLinkAbout = () =>{
        const forOpenn = document.getElementById('forOpen');
        const forClose = document.getElementById('forClose');
        const mainnav = document.getElementById('mainnav');
        forOpenn.style.zIndex = "10"
        forClose.style.zIndex = "-10";
        mainnav.style.left = "-100%"
    }
    
    const quickLinkProjects = () =>{
        const forOpenn = document.getElementById('forOpen');
        const forClose = document.getElementById('forClose');
        const mainnav = document.getElementById('mainnav');
        forOpenn.style.zIndex = "10"
        forClose.style.zIndex = "-10";
        mainnav.style.left = "-100%"
    }
    const quickLinkContacts = () =>{
        const forOpenn = document.getElementById('forOpen');
        const forClose = document.getElementById('forClose');
        const mainnav = document.getElementById('mainnav');
        forOpenn.style.zIndex = "10"
        forClose.style.zIndex = "-10";
        mainnav.style.left = "-100%"
    }



    return (
        <div className="navigation">
            <div className="forOpen" id="forOpen" onClick={forOpen} >
                <MenuIcon />
            </div>
            <div className="forClose" id="forClose" onClick={forClose}>
                <CloseIcon />
            </div>
            <div className="fixed-left-navbar" id="mainnav">
                <div className="main-container" >
                    <div className="hero-img">
                        <img src={hero2} />
                    </div>
                    <div className="hero-name">
                        <h2>Diyorbek</h2>
                    </div>
                    <div className="socials">
                        <div className="telegram">
                            <a href="https://t.me/l_investor"><TelegramIcon /></a>
                        </div>
                        <div className="facebook">
                        <a href="https://facebook.com/diyorbek0425"><FacebookIcon /></a>
                        </div>
                        <div className="instagram">
                        <a href="https://instagram.com/diyorbek_0425"> <InstagramIcon /> </a>
                        </div>
                        <div className="twitter">
                        <a href="https://t.me/l_investor">  <TwitterIcon /></a>
                        </div>
                        <div className="linkedin">
                        <a href="https://t.me/l_investor">   <LinkedInIcon /> </a>
                        </div>
                    </div>
                    <div className="quicklinks">
                        <ul>
                            <li><a href="#home" className="homeicon active" onClick={quickLinkHome}><HouseIcon fontSize="" /> Home</a></li>
                            <li><a href="#about" className="abouticon" onClick={quickLinkAbout}><PermIdentityIcon /> About</a></li>
                            <li><a href="#projects" className="resumeicon" onClick={quickLinkProjects}><InsertDriveFileIcon /> Projects</a></li>
                            <li><a href="#contact" className="contacticon" onClick={quickLinkContacts}><EmailIcon /> Contacts</a></li>
                        </ul>
                    </div>
                    <div className="tel_num">
                        <p>Tel: <span>+998 33 290 70 80</span></p>
                    </div>

                </div>
            </div>
        </div>
    );
};


export default Navigation;
