import React from 'react';
import './FactsS.scss'
import InsertEmoticonIcon from '@material-ui/icons/InsertEmoticon';
import BookIcon from '@material-ui/icons/Book';
import HeadsetIcon from '@material-ui/icons/Headset';
import PeopleIcon from '@material-ui/icons/People';
const Facts = () => {
    return (
        <div className="facts_sec">
            <div className="this_section">
                <h1>Facts</h1>  
            </div>
            <div className="parent_facts">
                <div className="happy_clients">
                    <div className="smile_icon">
                        <InsertEmoticonIcon />
                    </div>
                    <div className="fact1">
                        <h1>10</h1>
                        <h4>Happy clients</h4>
                    </div>
                </div>
                <div className="projects">
                    <div className="project_icon">
                        <BookIcon />
                    </div>
                    <div className="fact2">
                        <h1>10</h1>
                        <h4>Projects</h4>
                    </div>
                </div>
                <div className="support_hours">
                    <div className="headset_icon">
                        <HeadsetIcon />
                    </div>
                    <div className="fact3">
                        <h1>100</h1>
                        <h4>Support hours</h4>
                    </div>
                </div>
                <div className="partners">
                    <div className="hardworkers_icon">
                        <PeopleIcon />
                    </div>
                    <div className="fact4">
                        <h1>5</h1>
                        <h4>Hard Workers</h4>
                    </div>
                </div>
            </div>
        </div>
    );
};


export default Facts;